@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}
/* body{
    background-color: whitesmoke;
} */

.navbar{
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color:#565387 !important;

}
.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}
#card{
    height:430px;
}
#cardp{
    height:470px;
}
#cardimg{
    height:250px;
    width:314px;
}

#wcardimg{
    width: 314px;
    height: 225px;
}
#cardimgp{
    height:250px;
    width:314px;
}
#cardimgc{
    height:230px;
    width: 314px;
}
.icons{
    height:100px;
}

.fb{
    height:55px;
    width: 55px;
    border-radius: 100%;
    cursor: pointer;
    background-color: rgb(240, 239, 239);
    float: left;
    padding: 5px;
    margin-right:10px;
    margin-bottom: 3px;
    box-shadow: 0 1rem 1rem -0.7rem rgba(235, 27, 27, 0.4);

}
.fb:hover{

    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
    

    
}
.exp{
    margin-top: 25px;
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;

}
#image:hover{
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
}
  


.brand-name{
    color:#3498db !important
}

.navbar a:hover{
    color:#3498db !important;

}

@media(max-width:990px) 
{
    .navbar-brand{
        font-size: 1.5rem !important;
    }
    
}
#About:hover{
   color: #3498db !important;
}


#header{
    width:100%;
    height:80vh;
    margin-top: 70px;

}
#headerabout{
    width:100%;
    height:135vh;
    margin-top:30px;


}
#header h1{
    margin:0 0 10px 0;
    font-size: 38px;
    font-weight:200;
    color: black;
}
#header .brand-name{
color:#3498db;
font-size: 38px;
}
#header h2{
    color: #484848;
    font-size: 24px;
}
.header-img {
    text-align: right;
}

footer p {
    margin:0;
    padding:0;
}

a{
   margin:4px; 
}

.about{
    width:450px;
}

.animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);

    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);

    }
}
@keyframes up-down{
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);

    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);

    }

}


